.navbar {
    background-color: rgb(17, 8, 39);
    display: flex;
    justify-content: space-evenly;
}
.navbar a {
    color: white;
    font-size: 2rem;
    padding: 1rem;
    text-decoration: none;
}
.navbar a:hover {
    background-color: rgb(170, 27, 27);
    color: #fff;
    transition: all 0.5s;
}